/* MouseRing.module.css */
.mouseRing {
    position: fixed;
    top: 0;
    left: 0;
    width: 800px;
    height: 800px;
    pointer-events: none;
    border-radius: 50%;
    /* transform: translate(-100px, -100px); */
    transition: transform 0.05s linear;
    background: radial-gradient(
        circle,
        rgba(255, 255, 255, 0.05) 0%,
        transparent 60%
    );
    /* box-shadow: 0 0 20px 10px rgba(255, 255, 255, 0.1); */
    z-index: 9999;
}
